<script>
// import HelloWorld from "./components/HelloWorld.vue";np
import myCard from "./components/card";
export default {
  name: "LayoutDefault",

  components: {
    // HelloWorld,
    myCard,
  },

  data() {
    return {
      leftDrawerOpen: false,
      deferredPrompt: null,
      appWasInstled: false,
    };
  },
  mounted() {
    debugger
    let appWasInstled = localStorage.getItem("appWasInstled");
    if (appWasInstled) this.appWasInstled = true;
    window.addEventListener("beforeinstallprompt", (e) => {
      console.log("Ubeforeinstallprompt");

      e.preventDefault();
      this.deferredPrompt = e;
    });
  },
  methods: {
    installApp() {
      this.deferredPrompt.prompt();
      // Wait for the user to respond to the prompt
      this.deferredPrompt.userChoice.then((choiceResult) => {
        if (choiceResult.outcome === "accepted") {
          this.appWasInstled = true;
          localStorage.setItem("appWasInstled", true);
          this.showInstall = false;
          console.log("User accepted the A2HS prompt 1");
        } else {
          this.showInstall = false;
          console.log("User dismissed the A2HS prompt 2");
        }
        console.log("User accepted the A2HS prompt 3");
        this.deferredPrompt = null;
        // this.defthis.erredPrompt = null;
      });
    },
  },
};
</script>

